___CSS_LOADER_EXPORT___.push([module.id, `* {
    font-family: Roboto, 'Courier New', Courier, monospace;
    color: white;
}

html,
body {
    display: border-box;
    margin: 0;
    padding: 0;
}

body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #020617;
}

#main-content {
    height: 600px;
    width: 900px;
    display: grid;
    grid-template: 1fr 5fr/2fr 5fr;
    background-color: #1e1b4b;
    border-radius: 1em;
}

.todo-header {
    grid-area: 1/2/2/-1;
    background-color: #312e81;
    border-radius: 0 1em 0 0;

    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.todo-header .header-title {
    font-weight: 700;
    font-size: xx-large;
    margin-left: 0.5em;
}

.project-container {
    grid-area: 1/1/-1/2;
    background-color: #3730a3;
    border-radius: 1em 0 0 1em;
}

.todo-container {
    grid-area: 2/2/-1/-1;
    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    gap: 1em;
    padding: 2em;
}

.todo-container .todo-item {
    height: 50px;
    border-radius: 1em;
    background-color: #5b21b6;
